import React, { useEffect, useState, useRef } from 'react';
import { collection, getDocs, query, orderBy, doc, deleteDoc } from 'firebase/firestore';
import { firestore, auth } from '../firebase';
import ReactMarkdown from 'react-markdown';
import LoadingCircle from '../components/LoadingCircle';

const ShareChat = ({ selectedSession, closeShareChat, resetChat, closeChatHistoryOnMobile }) => {
    const [sharedChatData, setSharedChatData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const shareChatRef = useRef(null);

  useEffect(() => {
    const fetchSharedChatData = async () => {
      try {
        if (selectedSession) {
          const messagesCollectionRef = collection(firestore, 'users', auth.currentUser.uid, 'discussions', selectedSession, 'messages');
          const messagesSnapshot = await getDocs(query(messagesCollectionRef, orderBy('createTime')));
          const messagesData = messagesSnapshot.docs.map((doc) => {
            const { prompt, response } = doc.data();
            return { prompt, response };
          });
          setSharedChatData(messagesData);
          setIsLoading(false);
          console.log('Fetched messages for discussionId:', selectedSession, messagesData);
        }
      } catch (error) {
        console.error('Error fetching shared chat messages:', error);
        setIsLoading(false);
      }
    };

    fetchSharedChatData();
  }, [selectedSession]);

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (shareChatRef.current && !shareChatRef.current.contains(event.target)) {
        // Clicked outside the ShareChat component
        closeShareChat();
      }
    };
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [closeShareChat]);

  const handleDeleteSession = async () => {
    try {
      // Delete the selectedSession document from Firestore
      const sessionDocRef = doc(firestore, 'users', auth.currentUser.uid, 'discussions', selectedSession);
      await deleteDoc(sessionDocRef);
      console.log('Discussion deleted:', selectedSession);
      closeShareChat(); // Close the ShareChat component after deletion
      resetChat(); // Reset the chat by setting selectedSession to null
      startNewChat(); 
      closeChatHistoryOnMobile();
    } catch (error) {
      console.error('Error deleting discussion:', error);
    }
  };
  
  return (
    <div ref={shareChatRef}   className="blurred-background">
    <div className="white-box-chathistory">
      <div className="go-back-svg">
        <button onClick={closeShareChat}>
          <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} color="black" stroke="currentColor" className="w-6 h-6">
            <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
          </svg>
        </button>
      </div>

      <h2>Share link to Chat</h2>
      <div className='divider '></div>
      <p>
        Messages you send after creating your link won't be shared. Anyone with the URL will be able to view the shared chat.
      </p>
      <div className="shared-chat scroll-cat mb-5">
        {isLoading ? (
          <LoadingCircle />
        ) : (
          sharedChatData.map((message, index) => (
            <div key={index} className="chat-message">
              <strong>
                <ReactMarkdown>{message.prompt}</ReactMarkdown>

              </strong>
              <ReactMarkdown>{message.response}</ReactMarkdown>
              <div className='divider '></div>
            </div>
          ))
        )}
      </div>
      <div className='delete-svg' >
      <button className='delete-button' onClick={handleDeleteSession}>
      <svg xmlns="http://www.w3.org/2000/svg" color= '#212529' fill="none" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" height="25" width="25">
  <path strokeLinecap="round" strokeLinejoin="round" d="m14.74 9-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 0 1-2.244 2.077H8.084a2.25 2.25 0 0 1-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 0 0-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 0 1 3.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 0 0-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 0 0-7.5 0" />
</svg>
</button>
</div>
    </div>
    </div>
  );
};

export default ShareChat;
