import React, { useRef, useState, useEffect } from 'react';

const TopicPicker = ({ onSelectTopic, selectedTopic, toggleTopicPicker }) => {
  const dropdownRef = useRef(null);
  const [localSelectedTopic, setLocalSelectedTopic] = useState(null);
  const pickModelDiv = useRef(null);
  const [selectedTopicId, setSelectedTopicId] = useState(selectedTopic ? selectedTopic.id : null);
  const [isHovering, setIsHovering] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');

  const handleTopicSelection = (topic) => {
    setLocalSelectedTopic(topic);
    setSelectedTopicId(topic.id);
    onSelectTopic(topic);
    toggleTopicPicker();
  };

  const handleSearch = (event) => {
    setSearchQuery(event.target.value);
  };

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        toggleTopicPicker();
      }
    };
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [dropdownRef, toggleTopicPicker]);
  
  const TopicLabel = ({ category, handleTopicSelection, selectedTopicId }) => {
    const [isHovering, setIsHovering] = useState(false);
  
    const handleLabelClick = () => {
      handleTopicSelection(category);
      setIsHovering(false);
    };
  
    return (
      <button
        className="chat-dropdown-model-button"
        id={category.id}
        onClick={handleLabelClick}
        onMouseEnter={() => setIsHovering(true)}
        onMouseLeave={() => setIsHovering(false)}
      >
        {(category.id === selectedTopicId || category === selectedTopic) && (
          <div className="activated-indicator-topic"></div>
        )}
        {isHovering && category.id !== selectedTopicId && (
          <div className="hover-indicator"></div>
        )}
        <div className="icon-container" style={{ width: '3rem' }}>
          {category.icon}
        </div>
  
        <div className="topic-text-container">
          <div className="title-container"> {/* Add a container for the title */}
            <h5 className="m-0">{category.name}</h5>
          </div>
          <h6 className="text-muted  m-0">{category.description}</h6>
        </div>
      </button>
    );
  };
  
  const filteredCategories = categories.filter((category) => {
    return category.name.toLowerCase().includes(searchQuery.toLowerCase());
  });


  return (
    <div ref={dropdownRef} className={`chat-dropdown-model `}>
    <div className="topic-picker-heading-container">
      <h3 className="topic-picker-heading">Select a Topic</h3>
      <input
        type="text"
        placeholder="Search topics"
        value={searchQuery}
        onChange={handleSearch}
        className="topic-picker-search-bar"
      />
    </div>
      <div className='card-profile-border chat-topics-selection  scroll-col'>
       {filteredCategories.map((category) => (
          <TopicLabel
            key={category.id}
            category={category}
            handleTopicSelection={handleTopicSelection}
            selectedTopicId={selectedTopicId}
            selectedTopic={localSelectedTopic}
            toggleTopicPicker={toggleTopicPicker}
          />
        ))}
      </div>
    </div>
  );
};

export default TopicPicker;




   
