import { auth, db, firestore } from '../firebase';
import { collection, doc, setDoc, addDoc, getDoc, onSnapshot, getDocs, query, orderBy, serverTimestamp } from 'firebase/firestore';
import ReactMarkdown from 'react-markdown';
import LoadingCircle from '../components/LoadingCircle';
import React, { useState, useEffect, useRef } from 'react';
import ChatHistory from './ChatHistory';
import ChatExperts from './ChatExperts';
import TopicPicker from './TopicPicker.js';
import nlp from 'compromise/three';
import { Collapse } from 'react-bootstrap';
import { Fade } from 'react-bootstrap';



const ChatInterface = () => {
  const [message, setMessage] = useState('');
  const [textareaHeight, setTextareaHeight] = useState('28px');
  const textareaRef = useRef(null);
  const [chatMessages, setChatMessages] = useState([]);
  const maxTextareaHeight = 200;
  const [isHistoryOpen, setIsHistoryOpen] = useState(false);
  const [isExpertsOpen, setIsExpertsOpen] = useState(false);
  const chatContainerRef = useRef(null);
  const [isBotResponding, setIsBotResponding] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  const [selectedSession, setSelectedSession] = useState(null);  
  const [selectedTopic, setSelectedTopic] = useState(null); 
  const [sessionToUse, setSessionToUse] = useState(null);
  const [showGreeting, setShowGreeting] = useState(true);
  const [greetingVisible, setGreetingVisible] = useState(false);
  const [currentUser, setCurrentUser] = useState(null); 
  const [isFirstMessage, setIsFirstMessage] = useState(true);
  const [businessVisible, setBusinessVisible] = useState(false);
  const [showTopicPicker, setShowTopicPicker] = useState(false);
  const [expertsAvailable, setExpertsAvailable] = useState(false);

  const handleExpertsAvailable = (available) => {
    setExpertsAvailable(available);
  };
  

  const fetchMessages = async (userUid, discussionId) => {
    try {
      const messagesCollectionRef = collection(firestore, 'users', userUid, 'discussions', discussionId, 'messages');
      const messagesSnapshot = await getDocs(query(messagesCollectionRef, orderBy('createTime')));
      const messagesData = messagesSnapshot.docs.map((doc) => {
        const { prompt, response } = doc.data();
        return [
          { text: <ReactMarkdown>{prompt}</ReactMarkdown>, user: 'user' },
          { text: <ReactMarkdown>{response}</ReactMarkdown>, user: 'bot', isTyping: false },
        ];
      }).flat();
      setChatMessages(messagesData);
    } catch (error) {
    }
  };

  const handleSendMessage = async () => {
    if (message.trim() !== '') {
      setIsBotResponding(true);
      setShowGreeting(false);
      const uid = auth.currentUser.uid;
  
      try {
        let currentSessionToUse = selectedSession;
  
        if (!currentSessionToUse) {
          const words = message.split(' ').slice(0, 5); // Get the first five words of the message
          currentSessionToUse = words.join(' '); // Join the words with a space
  
          await setDoc(doc(db, `users/${uid}`), {});
  
          const userDocRef = doc(db, `users/${uid}/discussions/${currentSessionToUse}`);
          const discussionsSnapshot = await getDoc(userDocRef);
  
          if (!discussionsSnapshot.exists()) {
            const timestamp = serverTimestamp();
            await setDoc(userDocRef, { timestamp });
  
            setSessionToUse(currentSessionToUse);
            setSelectedAndFetchMessages(currentSessionToUse);
  
            sessionStorage.setItem('currentDiscussionId', currentSessionToUse);
          }
        }
  
        const userDocRef = doc(db, `users/${uid}/discussions/${currentSessionToUse}`);
        const messagesCollectionRef = collection(userDocRef, 'messages');
        const timestamp = serverTimestamp();
        const botMessageDocRef = await addDoc(messagesCollectionRef, { prompt: message, timestamp });
  
  
        setMessage('');
        handleTextareaResize();
        setChatMessages((prevChatMessages) => [
          ...prevChatMessages,
          { text: message, user: 'user' },
          { text: '', user: 'bot', isTyping: true },
        ]);

        if (isFirstMessage) {
          setIsFirstMessage(false);
        }
  
        const unsubscribe = onSnapshot(botMessageDocRef, async (docSnapshot) => {
          const rawBotResponse = docSnapshot.data()?.response;
          if (rawBotResponse) {
            unsubscribe();
            const initialTypingSpeed = 20;
            const speedIncreaseRate = 0.1;
            const formattedBotResponse = rawBotResponse;
            for (let i = 0; i < formattedBotResponse.length; i++) {
              const currentTypingSpeed = Math.max(5, initialTypingSpeed - i * speedIncreaseRate);
              await new Promise((resolve) => setTimeout(resolve, currentTypingSpeed));
              setChatMessages((prevChatMessages) => [
                ...prevChatMessages.slice(0, -1),
                {
                  text: <ReactMarkdown>{formattedBotResponse.slice(0, i + 1)}</ReactMarkdown>,
                  user: 'bot',
                },
              ]);
            }
            setIsBotResponding(false);
            if (window.innerWidth < 768 && textareaRef.current) {
              textareaRef.current.blur();
            }
          }
        });
  
        // Move setSelectedSession here, after the timestamp is set
        setSelectedSession(currentSessionToUse); 
      } catch (error) {
      }
    }
  };
  

  const handleSessionClick = async (discussionId) => {
    setSelectedSession(discussionId);
    setShowGreeting(false); 
    sessionStorage.setItem('lastDiscussionId', discussionId);

    await fetchMessages(auth.currentUser.uid, discussionId);
  };

  
  const setSelectedAndFetchMessages = async (sessionId) => {
    setSelectedSession(sessionId);
    sessionStorage.setItem('lastDiscussionId', sessionId);
    await fetchMessages(sessionId); // Add a function to fetch messages for the selected session
  };
  
  

  window.addEventListener('beforeunload', () => {
  sessionStorage.removeItem('currentDiscussionId');
  });

  useEffect(() => {
    const fetchLastDiscussionId = async () => {
      try {
        if (auth.currentUser) {
          const lastDiscussionId = sessionStorage.getItem('lastDiscussionId');
  
          if (lastDiscussionId) {
          } else {
          }
        } else {
        }
      } catch (error) {
      }
    };
  
    fetchLastDiscussionId(); 
    if (isOpen) {
      fetchSessions();
    }
  }, [isOpen]);

  const toggleHistory = () => {
    if (!isBotResponding) {
      setIsHistoryOpen(!isHistoryOpen);
      setIsExpertsOpen(false);
    }
  };
  
  const toggleExperts = () => {
    setIsExpertsOpen(!isExpertsOpen);
    setIsHistoryOpen(false);
  };

  const handleMessageChange = (e) => {
    setMessage(e.target.value);
    handleTextareaResize();
  };

  const handleTextareaResize = () => {
    const textarea = textareaRef.current;
    if (textarea) {
      textarea.style.height = '28px';
      const newHeight = Math.min(textarea.scrollHeight, maxTextareaHeight);
      textarea.style.height = `${newHeight}px`;
    }
  };

  
  useEffect(() => {
    handleTextareaResize();
    const handleKeyDown = (event) => {
      if (event.key === 'Enter' && !event.shiftKey) {
        event.preventDefault();
        handleSendMessage();
      }
    };
  
    const textareaElement = textareaRef.current;
    if (textareaElement) {
      textareaElement.addEventListener('keydown', handleKeyDown);
    }
    return () => {
      if (textareaElement) {
        textareaElement.removeEventListener('keydown', handleKeyDown);
      }
    };
  }, [chatMessages]);
  
  useEffect(() => {
    if (message === '') {
      setTextareaHeight('28px');
    }
  }, [message]);

  useEffect(() => {
    if (chatContainerRef.current) {
      chatContainerRef.current.scrollTop = chatContainerRef.current.scrollHeight;
    }
  }, [chatMessages]);

  const resetChatMessages = () => {
    setChatMessages([]);
  };

  useEffect(() => {
    const fetchCurrentUser = async () => {
      if (auth.currentUser) {
        setCurrentUser(auth.currentUser);
        setGreetingVisible(true);
  
        setTimeout(() => {
          setBusinessVisible(true);
        }, 700); // Set businessVisible to true after a 1.5-second delay
      }
    };
    fetchCurrentUser();
  }, []);
  
  
  
  const getGreeting = (hour) => {
    if (hour >= 5 && hour < 12) {
      return "Good morning";
    } else if (hour >= 12 && hour < 17) {
      return "Good afternoon";
    } else {
      return "Good evening";
    }
  };
  
  const toggleTopicPicker = () => {
    setShowTopicPicker(!showTopicPicker);
  };

  const handleTopicSelection = (topic) => {
    setSelectedTopic(topic);
    toggleTopicPicker();
  };

  const toggleDropdown = () => {
    setIsDropdownOpen(!isDropdownOpen);
    setIsIconRotated(!isIconRotated);
    toggleOpen();
  };

  
  
  return (
    <div className="Chatcontainer-model">



<ChatHistory isOpen={isHistoryOpen}   setShowGreeting={setShowGreeting}  toggleOpen={toggleHistory} onSessionClick={handleSessionClick} selectedSession={selectedSession}  resetChatMessages={resetChatMessages}/>
<div className='chat-display'>
{showTopicPicker && (
  <TopicPicker
  onSelectTopic={handleTopicSelection}
  selectedTopic={selectedTopic}
  toggleTopicPicker={toggleTopicPicker}
/>

)}        {/* Other components or content in your chat-display */}
  <div className='chat-column chat-bot-upper-section'>
 
  <div className="chat-box chat-bot-section" ref={chatContainerRef}>

  {showGreeting && currentUser && (
  <div className='chat-greeting-flex'>
    <>
      <Fade
        in={greetingVisible}
        appear={true}
        timeout={1500}
        className="greeting-fade" // Add a custom class for the greeting
      >
        <div className="chat-greeting">
          <h1>
            {getGreeting(new Date().getHours())}, {currentUser.displayName}
          </h1>
        </div>
      </Fade>
      <Fade
        in={businessVisible}
        appear={true}
        timeout={1000}
        delay={500}
        className="business-fade" // Add a custom class for the "Let's get down to business" text
      >
        <h3>Let's get down to business</h3>
      </Fade>
    </>
  </div>
)}


{chatMessages.map((chatMessage, index) => (
  <div key={index} className={`chat-bubble-bot ${chatMessage.user === 'user' ? 'user-sent-bot' : 'bot'}`}>
    {chatMessage.user === 'bot' && (
      <div className="bot-message">
        <div className="bot-avatar">
          <img
            className="bot-avatar"
            src={`${process.env.PUBLIC_URL}/images/arts/TIO.webp`}
            alt="TIO-bot"
          />
        </div>
        <div className="bot-details">
          <div className="bot-name">TIO-bot</div>
          {!isFirstMessage && chatMessage.isTyping ? ( // Check if it's the first message
            <LoadingCircle />
          ) : (
            <div className="chat-text">{chatMessage.text}</div>
          )}
        </div>
      </div>
    )}

    {chatMessage.user !== 'bot' && currentUser && ( // Check if currentUser is available
      <div className="bot-message"> {/* Define a CSS class for user's message */}
        <div className="bot-avatar">
          <img
            className="user-avatar"
            src={currentUser.photoURL} // Use currentUser.photoURL as the source
            alt="User Avatar"
          />
        </div>
        <div className="user-details">
          <div className="bot-name">You</div>
          <div className="chat-text">{chatMessage.text}</div>
        </div>
      </div>
    )}
  </div>
))}

      </div>
      <div className='chatui-wrapper'>
      <div className='spacing'>


      <button type="button" className="new-topic-btn rounded-pill"
      onClick={toggleTopicPicker}
      >
  
<svg xmlns="http://www.w3.org/2000/svg"  viewBox="0 -960 960 960"  color="white"
fill="none"
strokeWidth={1}
stroke="white"
height="30"
width="30"
>
  <path fill="white" d="M308.001-180.309v-406.998q0-27.023 18.5-45.857 18.5-18.835 45.423-18.835h407.767q26.527 0 45.417 18.891 18.891 18.89 18.891 45.417v264.306q0 12.855-4.616 24.504-4.615 11.65-14.211 21.246L682.365-134.828q-9.596 9.596-21.246 14.211-11.649 4.616-24.504 4.616H372.309q-26.527 0-45.417-18.891-18.891-18.89-18.891-45.417Zm-190-515.767q-4.615-25.923 11.135-48.505 15.75-22.581 41.711-27.033l400.383-71.385q25.923-4.615 48.004 11.135t26.534 41.711l10.231 58.462h-52.23L594-783.538q-.769-4.231-4.616-6.731-3.846-2.5-8.462-1.731l-401.537 71.769q-5.385.769-8.077 5-2.693 4.231-1.923 9.616l58.924 330.313v127.223q-14.846-5.846-25.385-18.581-10.538-12.735-13.538-29.034l-71.385-400.382ZM360-587.691v407.382q0 5.385 3.462 8.847 3.462 3.462 8.847 3.462H624l168-168v-251.691q0-5.385-3.462-8.847-3.462-3.462-8.847-3.462H372.309q-5.385 0-8.847 3.462-3.462 3.462-3.462 8.847ZM576-384Zm-25.747 25.999V-276q0 11.05 7.502 18.524 7.503 7.475 18.492 7.475t18.41-7.475q7.42-7.474 7.42-18.524v-82.001h81.689q10.984 0 18.609-7.418 7.624-7.418 7.624-18.384 0-10.966-7.475-18.455-7.474-7.49-18.524-7.49h-82.001v-82.168q0-11.085-7.478-18.584-7.479-7.499-18.534-7.499-11.056 0-18.521 7.499-7.465 7.499-7.465 18.584v82.168H468q-11.05 0-18.524 7.503-7.475 7.503-7.475 18.258 0 11.056 7.499 18.521 7.499 7.465 18.584 7.465h82.169Z"/></svg>


  <span className="button-text">Topic</span>
</button>

 
  </div>
      <div className="chatui-container">

      <textarea
  value={message}
  onChange={handleMessageChange}
  className="scroll-col message-input no-left"
  style={{ height: textareaHeight }} // Set the height dynamically
  ref={textareaRef}
  placeholder="Type a message..."
  autoComplete="off"
  inputMode="text"
  onKeyDown={(e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      if (message.trim() !== '') {
        handleSendMessage();
      }
    }
  }}
  disabled={isBotResponding}  // Ensure that the disabled prop is bound to isBotResponding
/>

<button
  aria-label="Send Message"
  className="svg-container-chat send-btn"
  disabled={isBotResponding || message.trim() === ''}
  onClick={() => {
    if (message.trim() !== '') {
      handleSendMessage(selectedSession);
    }
  }}
>
          <i className="fas fa-paper-plane"></i>
        </button>
        </div>
        </div>
        </div>
        </div>
     
        <div className={`chatbot-expert ${isExpertsOpen ? 'chatbot-expert-open' : ''}`}>

       <span aria-hidden="true" className='svg-chatbot-expert ' onClick={toggleExperts}>
       <svg
  xmlns="http://www.w3.org/2000/svg"
  fill={expertsAvailable ? '#fdfa72' : 'none'} // Set the fill color here
  strokeWidth={1}
  stroke={expertsAvailable ? 'black' : 'currentColor'} // Set the stroke color here
  viewBox="0 0 24 24"
  height="25"
  width="25"
  style={{ marginLeft: 'auto' }}
>
  <path strokeLinecap="round" strokeLinejoin="round" d="M12 18v-5.25m0 0a6.01 6.01 0 001.5-.189m-1.5.189a6.01 6.01 0 01-1.5-.189m3.75 7.478a12.06 12.06 0 01-4.5 0m3.75 2.383a14.406 14.406 0 01-3 0M14.25 18v-.192c0-.983.658-1.823 1.508-2.316a7.5 7.5 0 10-7.517 0c.85.493 1.509 1.333 1.509 2.316V18" />
</svg>

      </span>
  
      <ChatExperts
        isOpen={isExpertsOpen}
        toggleOpen={toggleExperts}
        selectedTopic={selectedTopic}
        selectedSession={selectedSession}
        onExpertsAvailable={handleExpertsAvailable}
      />
    </div>

</div>
      
  );
};

export default ChatInterface;