import React, { useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { auth, firestore } from '../firebase';
import { collection, doc, setDoc, getDoc } from 'firebase/firestore';

const ExpertBox = ({ isHovered, displayName, photoURL, skills, description, otherUserUID, occupation, onClose, selectedSession }) => {
  const navigate = useNavigate();
  const [currentUserUID, setCurrentUserUID] = useState(null);
  const discussionID = selectedSession;
  const [isConnected, setIsConnected] = useState(false);
  const [showIcon, setShowIcon] = useState(false);

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((user) => {
      if (user) {
        setCurrentUserUID(user.uid);
        console.log('Current User UID in ExpertBox:', user.uid);
      } else {
        console.error('User is not authenticated in ExpertBox');
        setCurrentUserUID(null);
      }
    });
    return () => {
      unsubscribe();
    };
  }, [selectedSession]);

  const handleChatButtonClick = async () => {
    try {
      if (!otherUserUID || !currentUserUID || !selectedSession) {
        throw new Error('otherUserUID, currentUserUID, or selectedSession is undefined or null');
      }
  
      // Combine currentUserUID and otherUserUID to create a unique discussionID
      const discussionID = `${currentUserUID}_${otherUserUID}`;
  
      // Create a new document in the sharedConversations collection
      const conversationRef = doc(collection(firestore, 'sharedConversations'), discussionID);
      
      // Use setDoc to create or update the document with the desired data
      await setDoc(conversationRef, {
        selectedSession,
        currentUserUID,
        otherUserUID,
        // Add other fields as needed
      });
  
      // Navigate to the chat page
      navigate('/chat', {
        state: {
          otherUserUID,
          selectedSession,
          currentUserUID,
        },
      });
  
      console.log('New document created successfully.');
    } catch (error) {
      console.error('Error handling chat button click:', error.message);
    }
  };
  

  const handleEscapeKey = (event) => {
    if (event.key === 'Escape') {
      onClose();
    }
  };

  useEffect(() => {
    if (isHovered) {
      document.addEventListener('keydown', handleEscapeKey);
    }

    return () => {
      document.removeEventListener('keydown', handleEscapeKey);
    };
  }, [isHovered]);


  const fetchUserContacts = async () => {
    try {
      const currentUserUID = auth.currentUser.uid;
      const userProfileRef = doc(firestore, 'userProfiles', currentUserUID);
      const expertApplicationRef = doc(firestore, 'expertApplications', currentUserUID);
  
      const [userProfileDoc, expertApplicationDoc] = await Promise.all([
        getDoc(userProfileRef),
        getDoc(expertApplicationRef),
      ]);
  
      let contacts = {};
  
      if (userProfileDoc.exists()) {
        const userData = userProfileDoc.data();
        contacts = userData.contacts || {};
      }
  
      if (expertApplicationDoc.exists()) {
        const expertData = expertApplicationDoc.data();
        const expertContacts = expertData.contacts || {};
        contacts = { ...contacts, ...expertContacts };
      }
  
      // Check if the otherUserUID is in the contacts
      if (otherUserUID && contacts[otherUserUID]) {
        setIsConnected(true);
      } else {
        setIsConnected(false);
      }
    } catch (error) {
      console.error('Error fetching user contacts:', error);
    }
  };


  useEffect(() => {
    if ( otherUserUID) {
      fetchUserContacts();
    }
  }, [otherUserUID]);

  const handleConnectButtonClick = async () => {
    try {
      const currentUserUID = auth.currentUser.uid;
  
      if (otherUserUID) {
        // Get the current user's document references
        const userProfileRef = doc(firestore, 'userProfiles', currentUserUID);
        const expertApplicationRef = doc(firestore, 'expertApplications', currentUserUID);
  
        // Fetch the current user's documents from both collections
        const [userProfileDoc, expertApplicationDoc] = await Promise.all([
          getDoc(userProfileRef),
          getDoc(expertApplicationRef),
        ]);
  
        let contacts = {};
  
        // Check if the current user's contacts are in the userProfiles collection
        if (userProfileDoc.exists()) {
          const userData = userProfileDoc.data();
          contacts = userData.contacts || {};
        }
  
        // Check if the current user's contacts are in the expertApplications collection
        if (expertApplicationDoc.exists()) {
          const expertData = expertApplicationDoc.data();
          contacts = expertData.contacts || {};
        }
  
        // Update the current user's contacts field with the otherUserUID
        const updateRef = contacts ? doc(firestore, 'userProfiles', currentUserUID) : doc(firestore, 'expertApplications', currentUserUID);
        await setDoc(
          updateRef,
          {
            contacts: { ...contacts, [otherUserUID]: true },
          },
          { merge: true }
        );
  
        setIsConnected(true);
      } else {
        console.log('Other User UID not found');
      }
    } catch (error) {
      console.error('Error connecting:', error);
    }
  };
  


  return (
    isHovered && (
      
<div className="white-box" >
        <div className="go-back-svg go-back-svg-padding">
    <button onClick={onClose}>                  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} color="black" stroke="currentColor" className="w-6 h-6">
          <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
        </svg>
      </button>
    </div>

          <h5 className="card-title"> {displayName}</h5>
  
          <h5 className="card-title">Expertise: {occupation}</h5>
          
  <div href={photoURL} className="d-md-inline">
    <img
      src={photoURL}
      alt="Profile Photo"
      className="searchimage card-img user-avatar large-avatar rounded-circle mb-2 mx-md-auto mx-0 " 
    />
  </div>
  

          <div className="about-container">
          <h5 className="card-title"   style={{ display: 'inline' }}>About: </h5> 
<p style={{ display: 'inline' }}>{description}</p>
</div>
         

          <div style={{ marginBottom: '0.4rem', marginTop: '0.4rem' }}>
     
          <p className="font-weight-bold mb-0">Skills: {skills.join(', ')}</p>
  {skills && skills.map((skills) => (
    <button
      key={skills}
      type="button"
      className=" containerhover keyword-container mb-2 mx-1 pb-1"
    >
      {skills}
    </button>
  ))}

</div>

        <div>
       
        </div>
        <div className="d-flex justify-content-center">
        <Link to="/chat" state={{ otherUserUID, selectedSession }} onClick={handleChatButtonClick}>

            <button type="button" className="btn btn-primary btn-sm rounded-pill btncard pb-1 mb-2 mx-1" >
             
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" width="25" className="me-2 svg-inline--fa ">
                <path fillRule="evenodd" d="M4.804 21.644A6.707 6.707 0 006 21.75a6.721 6.721 0 003.583-1.029c.774.182 1.584.279 2.417.279 5.322 0 9.75-3.97 9.75-9 0-5.03-4.428-9-9.75-9s-9.75 3.97-9.75 9c0 2.409 1.025 4.587 2.674 6.192.232.226.277.428.254.543a3.73 3.73 0 01-.814 1.686.75.75 0 00.44 1.223zM8.25 10.875a1.125 1.125 0 100 2.25 1.125 1.125 0 000-2.25zM10.875 12a1.125 1.125 0 112.25 0 1.125 1.125 0 01-2.25 0zm4.875-1.125a1.125 1.125 0 100 2.25 1.125 1.125 0 000-2.25z" clipRule="evenodd" />
              </svg>
              <span className="me-2">Message</span> 
            </button>
        
            </Link>
            {currentUserUID !== otherUserUID && (
            <button
  type="button"
  className="btn btn-secondary btn-sm rounded-pill btncard pb-1 mb-2 mx-1"
  onClick={handleConnectButtonClick}
  disabled={isConnected}
>
  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" width="25" className="me-2 svg-inline--fa ">
    <path d="M6.25 6.375a4.125 4.125 0 118.25 0 4.125 4.125 0 01-8.25 0zM3.25 19.125a7.125 7.125 0 0114.25 0v.003l-.001.119a.75.75 0 01-.363.63 13.067 13.067 0 01-6.761 1.873c-2.472 0-4.786-.684-6.76-1.873a.75.75 0 01-.364-.63l-.001-.122zM19.75 7.5a.75.75 0 00-1.5 0v2.25H16a.75.75 0 000 1.5h2.25v2.25a.75.75 0 001.5 0v-2.25H22a.75.75 0 000-1.5h-2.25V7.5z" />
  </svg>
  <span className="me-2">{isConnected ? 'Connected' : 'Connect'}</span>
</button>

)}

</div>
</div>


   
    )
  );
};

export default ExpertBox;
